* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

.Citrus-container {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	overflow-y: auto !important;
	overflow-x: hidden;
}

.Citrus-Header {
	padding: 20px 0 10px 0;
	width: 90%;
	margin: auto;
}

.Citrus-Header > h2 {
	cursor: pointer;
	font-weight: lighter;
}

.Citrus-content {
	flex-grow: 1;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}

.Citrus-portfolio {
	display: flex;
	align-items: center;
	justify-content: center;
	padding-bottom: 20px;
}

.Citrus-portfolio-item {
	width: 150px;
	height: 400px;
	cursor: pointer;
	margin: 10px;
}

.Citrus-item-title {
	font-weight: lighter;
	font-size: 40px;
	padding: 10px;
	writing-mode: vertical-rl;
	text-orientation: mixed;
}

.Citrus-footer {
	padding: 0 0 20px 0;
	width: 90%;
	margin: auto;
}

.Citrus-footer > span {
	padding-right: 15px;
	font-size: 16px;
	text-decoration: none;
	color: white;
	font-weight: lighter;
}

.item-1 {
	/* background-image: url(../pictures/Citrus.jpg);
    background-size: cover; */
	background-color: rgb(231, 172, 182);
	display: flex;
	align-items: flex-end;
}

.item-2 {
	background-image: url(../pictures/Supreme.jpg);
	background-size: cover;
	display: flex;
	align-items: flex-start;
}

.item-3 {
	background-image: url(../pictures/Watson.jpg);
	background-size: cover;
	display: flex;
	align-items: flex-end;
}

.item-4 {
	background-image: url(../pictures/erscorp.jpg);
	background-size: cover;
	display: flex;
	align-items: flex-start;
}

.Citrus-body-1 {
	background: whitesmoke;
	color: black;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	flex-grow: 2;
	width: 100%;
	margin: auto;
}

.Citrus-body-1-header {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding-bottom: 40px;
	padding-top: 15px;
	border-bottom: 1px solid gray;
	background-color: rgb(231, 172, 182);
}

.Citrus-body-1-header-title {
	width: 60%;
	margin: auto;
}

.Citrus-body-1-header-title > h1 {
	font-size: 50px;
	font-weight: lighter;
}

.Citrus-body-1-header-title > h3 {
	font-weight: lighter;
}

.Citrus-body-1-header-img-container {
	padding: 20px 15px 0 15px;
	display: flex;
	align-items: flex-end;
	justify-content: center;
	position: relative;
}

.Citrus-body-1-img1 {
	width: 100%;
	max-width: 800px;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.Citrus-body-1-img2 {
	position: absolute;
	bottom: -25px;
	right: 30px;
	border: 1px solid white;
	margin-left: 30px;
	height: 300px;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.Citrus-body-1-about {
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	padding-top: 15px;
	padding-bottom: 15px;
	border-bottom: 1px solid gray;
}

.Citrus-body-1-about-title {
	width: 60%;
	margin: auto;
}

.Citrus-body-1-about-title > h1 {
	font-weight: lighter;
}

.Citrus-body-1-about-info {
	width: 60%;
	margin: auto;
}

.Citrus-body-1-about-info > p {
	font-weight: lighter;
	font-size: 18px;
	line-height: 1.6;
}

.Citrus-body-1-learning {
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	padding-top: 15px;
	padding-bottom: 15px;
	border-bottom: 1px solid gray;
}

.Citrus-body-1-learning-title {
	width: 60%;
	margin: auto;
}
.Citrus-body-1-learning-title > h1 {
	font-weight: lighter;
}

.Citrus-body-1-learning-info {
	width: 60%;
	margin: auto;
}

.Citrus-body-1-learning-info > p {
	font-weight: lighter;
	font-size: 18px;
	line-height: 1.6;
}

.Citrus-body-1-learning-info > h2 {
	padding-top: 15px;
	font-weight: lighter;
}

.Citrus-body-1-learning-info > ul {
	padding: 5px 0 0 60px;
}

.Citrus-body-1-features {
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	/* padding-top: 15px;
	padding-bottom: 15px; */
	border-bottom: 1px solid gray;
}

.Citrus-body-1-features-1 {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	border-bottom: 1px solid gray;
	padding: 15px 0;
}

.Citrus-body-1-features-1-info {
	width: 60%;
	margin: auto;
}
.Citrus-body-1-features-1-info > h1 {
	font-weight: lighter;
}

.Citrus-body-1-features-1-info > p {
	font-weight: lighter;
	font-size: 18px;
	line-height: 1.6;
}

.Citrus-body-1-features-1-img-container {
	padding: 20px 15px 0 15px;
}

/* 2 */

.Citrus-body-1-features-2 {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 15px 0;
	border-bottom: 1px solid gray;
}

.Citrus-body-1-features-2-info {
	width: 60%;
	margin: auto;
}
.Citrus-body-1-features-2-info > h1 {
	font-weight: lighter;
}

.Citrus-body-1-features-2-info > p {
	font-weight: lighter;
	font-size: 18px;
	line-height: 1.6;
}

.Citrus-body-1-features-2-img-container {
	padding: 20px 15px 0 15px;
}

/* 3 */

.Citrus-body-1-features-3 {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 15px 0;
}

.Citrus-body-1-features-3-info {
	width: 60%;
	margin: auto;
}
.Citrus-body-1-features-3-info > h1 {
	font-weight: lighter;
}

.Citrus-body-1-features-3-info > p {
	font-weight: lighter;
	font-size: 18px;
	line-height: 1.6;
}

.Citrus-body-1-features-3-img-container {
	padding: 20px 15px 0 15px;
}

.responsiveGif {
	width: 100%;
	max-width: 800px;
}

@media only screen and (max-width: 1035px) {
	/* .Citrus-body-1-img1 {
		width: 650px;
	}
	.Citrus-body-1-img2 {
		height: 300px;
	} */
}

@media only screen and (max-width: 840px) {
	/* .Citrus-body-1-img1 {
		width: 550px;
	}
	.Citrus-body-1-img2 {
		height: 250px;
	} */
}

@media only screen and (max-width: 768px) {
	.Citrus-portfolio {
		flex-direction: column;
	}

	.Citrus-portfolio-item {
		width: 400px;
		height: 60px;
		cursor: pointer;
		margin: 10px;
	}

	.Citrus-item-title {
		writing-mode: horizontal-tb;
	}

	.item-1,
	.item-2,
	.item-3,
	.item-4 {
		align-items: center;
	}

	.Citrus-item-title {
		padding: 0;
	}

	.Citrus-body-1-img1 {
		width: 100%;
	}
	.Citrus-body-1-img2 {
		height: 250px;
		position: absolute;
		bottom: -25px;
		right: 30px;
		border: 1px solid white;
	}
}

@media only screen and (max-width: 425px) {
	.Citrus-portfolio-item {
		width: 100%;
	}

	.Citrus-body-1-img1 {
		width: 100%;
	}
	.Citrus-body-1-img2 {
		height: 200px;
		position: absolute;
		bottom: -25px;
		right: 30px;
		border: 1px solid white;
	}
}
