* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

.Home-container {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
}

.Home-Header {
	padding: 20px 0 0 0;
	width: 90%;
	margin: auto;
}

.Home-scroll {
	flex-grow: 1;
	display: flex;
	align-items: center;
	justify-content: center;
}

.block {
	width: 200px;
	height: 460px;
	cursor: pointer;
	margin: 10px;
}

.block-1 {
	/* background-image: url(../pictures/Citrus.jpg);
	background-size: cover; */
	background-color: rgb(231, 172, 182);
	display: flex;
	align-items: flex-end;
}

.block-1:hover {
	transform: scale(1.5);
}

.block-2 {
	background-image: url(../pictures/Supreme.jpg);
	background-size: cover;
	display: flex;
	align-items: flex-start;
}

.block-2:hover {
	transform: scale(1.5);
}

.block-3 {
	background-image: url(../pictures/Watson.jpg);
	background-size: cover;
	display: flex;
	align-items: flex-end;
}

.block-3:hover {
	transform: scale(1.5);
}

.block-4 {
	background-image: url(../pictures/erscorp.jpg);
	background-size: cover;
	display: flex;
	align-items: flex-start;
}

.block-4:hover {
	transform: scale(1.5);
}

.Home-socials {
	padding: 0 0 20px 0;
	width: 90%;
	margin: auto;
}

.Home-socials > span {
	padding-right: 15px;
	font-size: 18px;
	text-decoration: none;
	color: white;
	font-weight: lighter;
}

.Home-link {
	text-decoration: none;
	color: white;
}

.Home-block-title {
	font-weight: lighter;
	font-size: 64px;
	padding: 10px;
	writing-mode: vertical-rl;
	text-orientation: mixed;
}

.Home-Header > h2 {
	cursor: pointer;
	font-weight: lighter;
}

@media only screen and (max-width: 425px) {
	.Home-block-title {
		padding: 0;
		font-size: 50px;
	}
}
